<template>
  <v-detail :songs="isongs" :singer="topList" :rank="true"></v-detail>
</template>
<script type="text/javascript">
  import {mapGetters} from 'vuex'
  import {getMusicRankList} from 'api/rank'
  import {ERR_OK} from 'api/config'
  import {createSong} from 'common/js/song'
  import VDetail from 'base/detail/detail'

  export default{
    data() {
      return {
        isongs: []
      }
    },
    mounted() {
    },
    computed: {
      ...mapGetters([
        'topList'
      ])
    },
    created() {
      // console.log(this.topList.id)
      this._getMusicRankList()
    },
    methods: {
      _getMusicRankList() {
        console.log(this.topList.id)
        // const id = this.topList.id ? this.topList.id : false
        if (!this.topList.id) {
          this.$router.push({
            path: '/rank'
          })
          return
        }
        getMusicRankList(this.topList.id).then((res) => {
          if (res.code === ERR_OK) {
            this.isongs = this._normaSongs(res.songlist)
            // this.isongs = this._normaSongs(this.isongs)
            // console.log(res)
          }
        })
      },
      _normaSongs(list) {
        let ret = []
        list.forEach((item) => {
          let musicData = item.data
          if (musicData.songid && musicData.albummid) {
            ret.push(createSong(musicData))
          }
        })
        return ret
      },
      _balck() {
        this.$router.back()
      },
      _randomPlay() {
        this.randomPlay({
          list: this.isongs
        })
      }
    },
    components: {
      VDetail
    }
  }
</script>


